.nav {
  width: 100%;
  min-height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #fff;
  background-color: #333;
}
.logo a {
  text-decoration: none;
}
.nav a {
  text-decoration: none;
  color: #fff;
}
.nav a:hover {
  text-decoration: underline;
}
.nav-list {
  width: 50%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.nav-list a:hover {
  text-decoration: underline;
}
.main {
  text-align: center;
  line-height: 2;
  color: #000;
}
